.articles_page_wrapper {
    height: 100vh;
    padding: 1rem 0;
    overflow-y: auto;
    width: 800px;
    margin: auto;
}


@media only screen and (max-width: 900px){
    .articles_page_wrapper {
        width: calc(100% - 2rem);
    }
}
.loading {

}

.article_item {
    display: flex;
    align-items: flex-start;
    padding: 1rem;
}

.article_image_wrapper {
    /*min-width: 200px;*/
    height: 120px;
    overflow: hidden;
    /*max-width: 200px;*/
}

.article_image_wrapper img {
    width: 100%;
}

.article_content {
    display: flex!important;
    flex-direction: column;
    align-items: flex-start;
    padding: 0.5rem;
}

.article_title {
    font-weight: 600;
    text-align: left;
    font-size: 1rem;
    color: #1e2a3a!important;
}

.article_description {
    font-style: italic;
    text-align: left;
    width: 100%;
}

.articles_page_search {
    height: 60px;
}

.articles_page_body {
    height: calc(100% - 60px);
    overflow-y: auto;
}

.search_input .ant-input-affix-wrapper {
    padding: 0 0.5rem;
}
.search_input input{
    padding: 0.5rem!important;
    height: 50px;
}

.search_input button {
    width: 50px;
    height: 50px;
}